import { useState } from 'react';
import PropTypes from 'prop-types';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    TextField,
    Button,
    Box,
    Typography,
    InputAdornment,
} from '@mui/material';
import { ArrowRight, DollarSign, User, MessageSquare } from 'lucide-react';

const RequestMoneyDialog = ({ open, onClose, onRequestMoney }) => {
    const [request, setRequest] = useState({
        from: '',
        amount: '',
        description: '',
    });
    const [errors, setErrors] = useState({});

    const handleChange = (e) => {
        const { name, value } = e.target;
        setRequest(prev => ({ ...prev, [name]: value }));
        setErrors(prev => ({ ...prev, [name]: '' }));
    };

    const validateForm = () => {
        const newErrors = {};
        if (!request.from.trim()) newErrors.from = 'Recipient is required';

        if (!request.amount || isNaN(request.amount) || Number(request.amount) <= 0) {
            newErrors.amount = 'Please enter a valid amount';
        }

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleRequestMoney = () => {
        if (validateForm()) {
            onRequestMoney(request);
            onClose();
            setRequest({ from: '', amount: '', description: '' });
        }
    };

    return (
        <Dialog
            open={open}
            onClose={onClose}
            PaperProps={{
                sx: {
                    backgroundColor: 'rgba(26, 27, 30, 0.95)',
                    backdropFilter: 'blur(10px)',
                    border: '1px solid rgba(255, 255, 255, 0.1)',
                    borderRadius: 2,
                    boxShadow: '0 4px 30px rgba(0, 0, 0, 0.1)',
                }
            }}
            maxWidth="sm"
            fullWidth
        >
            <DialogTitle sx={{ color: 'white', borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                <Typography variant="h6" component="div">
                    Request Money
                </Typography>
            </DialogTitle>

            <DialogContent sx={{ mt: 2 }}>
                <Box component="form" noValidate autoComplete="off">
                    <TextField
                        autoFocus
                        margin="dense"
                        name="from"
                        label="Request From"
                        type="text"
                        fullWidth
                        value={request.from}
                        onChange={handleChange}
                        error={!!errors.from}
                        helperText={errors.from}
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <User size={20} color="#2DD4BF" />
                                </InputAdornment>
                            ),
                        }}
                        sx={{
                            mb: 2,
                            '& .MuiOutlinedInput-root': {
                                color: 'white',
                                '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                '&:hover fieldset': { borderColor: '#2DD4BF' },
                                '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                            },
                            '& .MuiInputLabel-root': {
                                color: 'rgba(255, 255, 255, 0.7)',
                                '&.Mui-focused': { color: '#2DD4BF' },
                            },
                            '& .MuiFormHelperText-root': {
                                color: 'error.main',
                            },
                        }}
                    />

                    <TextField
                        margin="dense"
                        name="amount"
                        label="Amount"
                        type="number"
                        fullWidth
                        value={request.amount}
                        onChange={handleChange}
                        error={!!errors.amount}
                        helperText={errors.amount}
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <DollarSign size={20} color="#2DD4BF" />
                                </InputAdornment>
                            ),
                        }}
                        sx={{
                            mb: 2,
                            '& .MuiOutlinedInput-root': {
                                color: 'white',
                                '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                '&:hover fieldset': { borderColor: '#2DD4BF' },
                                '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                            },
                            '& .MuiInputLabel-root': {
                                color: 'rgba(255, 255, 255, 0.7)',
                                '&.Mui-focused': { color: '#2DD4BF' },
                            },
                            '& .MuiFormHelperText-root': {
                                color: 'error.main',
                            },
                        }}
                    />

                    <TextField
                        margin="dense"
                        name="description"
                        label="Description (Optional)"
                        type="text"
                        fullWidth
                        multiline
                        rows={3}
                        value={request.description}
                        onChange={handleChange}
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <MessageSquare size={20} color="#2DD4BF" />
                                </InputAdornment>
                            ),
                        }}
                        sx={{
                            '& .MuiOutlinedInput-root': {
                                color: 'white',
                                '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                '&:hover fieldset': { borderColor: '#2DD4BF' },
                                '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                            },
                            '& .MuiInputLabel-root': {
                                color: 'rgba(255, 255, 255, 0.7)',
                                '&.Mui-focused': { color: '#2DD4BF' },
                            },
                        }}
                    />
                </Box>
            </DialogContent>

            <DialogActions sx={{ borderTop: '1px solid rgba(255, 255, 255, 0.1)', p: 2 }}>
                <Button
                    onClick={onClose}
                    sx={{
                        color: 'rgba(255, 255, 255, 0.7)',
                        '&:hover': {
                            backgroundColor: 'rgba(255, 255, 255, 0.05)',
                        },
                    }}
                >
                    Cancel
                </Button>

                <Button
                    onClick={handleRequestMoney}
                    variant="contained"
                    startIcon={<ArrowRight size={20} />}
                    sx={{
                        bgcolor: '#2DD4BF',
                        color: '#1A1B1E',
                        '&:hover': {
                            bgcolor: '#26b5a3',
                        },
                    }}
                >
                    Request Money
                </Button>
            </DialogActions>
        </Dialog>
    );
};

RequestMoneyDialog.propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onRequestMoney: PropTypes.func.isRequired,
};

export default RequestMoneyDialog;

