import { useState } from 'react';
import PropTypes from 'prop-types';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    TextField,
    Button,
    Box,
    Typography,
    InputAdornment,
    Stepper,
    Step,
    StepLabel,
    Alert,
    List,
    ListItem,
    ListItemText,
    ListItemIcon,
    Avatar,
    Chip,
} from '@mui/material';
import { ArrowRight, DollarSign, MessageSquare, Users, Clock, Check, X } from 'lucide-react';

const CONFIRMATION_TIME = 300; // 5 minutes in seconds
const SERVICE_FEE = 0.0318; // 3.18%

const GroupRequestMoneyDialog = ({ open, onClose, group, onRequestMoney }) => {
    const [activeStep, setActiveStep] = useState(0);
    const [request, setRequest] = useState({
        amount: '',
        description: '',
        splitType: 'equal', // 'equal' or 'custom'
    });
    const [confirmations, setConfirmations] = useState({});
    const [timeLeft, setTimeLeft] = useState(CONFIRMATION_TIME);
    const [errors, setErrors] = useState({});

    // Mock group members data
    const groupMembers = [
        { id: 1, name: 'John Doe', status: 'pending' },
        { id: 2, name: 'Jane Smith', status: 'pending' },
        { id: 3, name: 'Alice Johnson', status: 'pending' },
    ];

    const handleChange = (e) => {
        const { name, value } = e.target;
        setRequest(prev => ({ ...prev, [name]: value }));
        setErrors(prev => ({ ...prev, [name]: '' }));
    };

    const validateForm = () => {
        const newErrors = {};

        if (!request.amount || isNaN(request.amount) || Number(request.amount) <= 0) {
            newErrors.amount = 'Please enter a valid amount';
        }

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleNext = () => {
        if (activeStep === 0 && !validateForm()) return;
        setActiveStep((prevStep) => prevStep + 1);

        // Simulate sending WhatsApp messages when moving to confirmation step
        if (activeStep === 0) {
            // In a real app, this would trigger WhatsApp notifications
            console.log('Sending WhatsApp notifications to group members...');
        }
    };

    const handleBack = () => {
        setActiveStep((prevStep) => prevStep - 1);
    };

    const calculateTotalWithFee = () => {
        const amount = Number(request.amount);
        const fee = amount * SERVICE_FEE;
        return (amount + fee).toFixed(2);
    };

    const calculateIndividualShare = () => {
        const total = Number(request.amount);
        return (total / groupMembers.length).toFixed(2);
    };

    // Simulate member confirmation
    const handleMemberConfirmation = (memberId, status) => {
        setConfirmations(prev => ({
            ...prev,
            [memberId]: status
        }));
    };

    const getStepContent = (step) => {
        switch (step) {
            case 0:
                return (
                    <Box sx={{ mt: 2 }}>
                        <TextField
                            autoFocus
                            margin="dense"
                            name="amount"
                            label="Amount to Split"
                            type="number"
                            fullWidth
                            value={request.amount}
                            onChange={handleChange}
                            error={!!errors.amount}
                            helperText={errors.amount}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <DollarSign size={20} color="#2DD4BF" />
                                    </InputAdornment>
                                ),
                            }}
                            sx={{
                                mb: 2,
                                '& .MuiOutlinedInput-root': {
                                    color: 'white',
                                    '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                    '&:hover fieldset': { borderColor: '#2DD4BF' },
                                    '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                                },
                                '& .MuiInputLabel-root': {
                                    color: 'rgba(255, 255, 255, 0.7)',
                                    '&.Mui-focused': { color: '#2DD4BF' },
                                },
                            }}
                        />

                        <TextField
                            margin="dense"
                            name="description"
                            label="Description"
                            type="text"
                            fullWidth
                            multiline
                            rows={3}
                            value={request.description}
                            onChange={handleChange}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <MessageSquare size={20} color="#2DD4BF" />
                                    </InputAdornment>
                                ),
                            }}
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    color: 'white',
                                    '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                    '&:hover fieldset': { borderColor: '#2DD4BF' },
                                    '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                                },
                                '& .MuiInputLabel-root': {
                                    color: 'rgba(255, 255, 255, 0.7)',
                                    '&.Mui-focused': { color: '#2DD4BF' },
                                },
                            }}
                        />

                        <Box sx={{ mt: 2 }}>
                            <Typography variant="subtitle2" color="rgba(255, 255, 255, 0.7)">
                                Summary
                            </Typography>

                            <List dense>
                                <ListItem>
                                    <ListItemText
                                        primary={`Individual Share: $${calculateIndividualShare()}`}
                                        secondary={`Total with ${(SERVICE_FEE * 100).toFixed(2)}% fee: $${calculateTotalWithFee()}`}
                                        primaryTypographyProps={{ color: 'white' }}
                                        secondaryTypographyProps={{ color: 'rgba(255, 255, 255, 0.7)' }}
                                    />
                                </ListItem>
                            </List>
                        </Box>
                    </Box>
                );
            case 1:
                return (
                    <Box sx={{ mt: 2 }}>
                        <Alert
                            severity="info"
                            sx={{
                                mb: 2,
                                backgroundColor: 'rgba(45, 212, 191, 0.1)',
                                color: 'white',
                                '& .MuiAlert-icon': { color: '#2DD4BF' }
                            }}
                        >
                            Waiting for group members to confirm the split...
                        </Alert>

                        <List>
                            {groupMembers.map((member) => (
                                <ListItem key={member.id}>
                                    <ListItemIcon>
                                        <Avatar sx={{ bgcolor: '#2DD4BF' }}>
                                            {member.name.charAt(0)}
                                        </Avatar>
                                    </ListItemIcon>

                                    <ListItemText
                                        primary={member.name}
                                        secondary={`Share: $${calculateIndividualShare()}`}
                                        primaryTypographyProps={{ color: 'white' }}
                                        secondaryTypographyProps={{ color: 'rgba(255, 255, 255, 0.7)' }}
                                    />

                                    <Chip
                                        icon={confirmations[member.id] === 'confirmed' ? <Check size={16} /> :
                                            confirmations[member.id] === 'rejected' ? <X size={16} /> :
                                                <Clock size={16} />}
                                        label={confirmations[member.id] || 'Pending'}
                                        color={confirmations[member.id] === 'confirmed' ? 'success' :
                                            confirmations[member.id] === 'rejected' ? 'error' :
                                                'default'}
                                        sx={{
                                            backgroundColor: confirmations[member.id] === 'confirmed' ? 'rgba(45, 212, 191, 0.2)' :
                                                confirmations[member.id] === 'rejected' ? 'rgba(255, 99, 99, 0.2)' :
                                                    'rgba(255, 255, 255, 0.1)',
                                            color: confirmations[member.id] === 'confirmed' ? '#2DD4BF' :
                                                confirmations[member.id] === 'rejected' ? '#ff6363' :
                                                    'white',
                                        }}
                                    />
                                </ListItem>
                            ))}
                        </List>

                        {/* Simulated confirmation buttons (for demo purposes) */}
                        <Box sx={{ mt: 2, display: 'flex', gap: 1 }}>
                            <Button
                                size="small"
                                onClick={() => handleMemberConfirmation(1, 'confirmed')}
                                sx={{ color: '#2DD4BF' }}
                            >
                                Simulate Confirm
                            </Button>

                            <Button
                                size="small"
                                onClick={() => handleMemberConfirmation(1, 'rejected')}
                                sx={{ color: '#ff6363' }}
                            >
                                Simulate Reject
                            </Button>
                        </Box>
                    </Box>
                );
            default:
                return null;
        }
    };

    return (
        <Dialog
            open={open}
            onClose={onClose}
            maxWidth="sm"
            fullWidth
            PaperProps={{
                sx: {
                    backgroundColor: 'rgba(26, 27, 30, 0.95)',
                    backdropFilter: 'blur(10px)',
                    border: '1px solid rgba(255, 255, 255, 0.1)',
                    borderRadius: 2,
                    boxShadow: '0 4px 30px rgba(0, 0, 0, 0.1)',
                }
            }}
        >
            <DialogTitle sx={{ borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Users size={24} color="#2DD4BF" />

                    <Typography variant="h6" component="div" sx={{ color: 'white' }}>
                        Split Bill with Group
                    </Typography>
                </Box>
            </DialogTitle>

            <DialogContent>
                <Stepper
                    activeStep={activeStep}
                    sx={{
                        mt: 2,
                        '& .MuiStepLabel-label': {
                            color: 'rgba(255, 255, 255, 0.7)',
                            '&.Mui-active': { color: '#2DD4BF' },
                            '&.Mui-completed': { color: '#2DD4BF' },
                        },
                        '& .MuiStepIcon-root': {
                            color: 'rgba(255, 255, 255, 0.2)',
                            '&.Mui-active': { color: '#2DD4BF' },
                            '&.Mui-completed': { color: '#2DD4BF' },
                        },
                    }}
                >
                    <Step>
                        <StepLabel>Set Amount</StepLabel>
                    </Step>

                    <Step>
                        <StepLabel>Confirm Split</StepLabel>
                    </Step>
                </Stepper>

                {getStepContent(activeStep)}
            </DialogContent>

            <DialogActions sx={{ borderTop: '1px solid rgba(255, 255, 255, 0.1)', p: 2 }}>
                <Button
                    onClick={onClose}
                    sx={{
                        color: 'rgba(255, 255, 255, 0.7)',
                        '&:hover': {
                            backgroundColor: 'rgba(255, 255, 255, 0.05)',
                        },
                    }}
                >
                    Cancel
                </Button>

                {activeStep > 0 && (
                    <Button
                        onClick={handleBack}
                        sx={{
                            color: '#2DD4BF',
                            '&:hover': {
                                backgroundColor: 'rgba(45, 212, 191, 0.1)',
                            },
                        }}
                    >
                        Back
                    </Button>
                )}

                <Button
                    onClick={handleNext}
                    variant="contained"
                    startIcon={<ArrowRight size={20} />}
                    sx={{
                        bgcolor: '#2DD4BF',
                        color: '#1A1B1E',
                        '&:hover': {
                            bgcolor: '#26b5a3',
                        },
                    }}
                >
                    {activeStep === 0 ? 'Continue' : 'Finish'}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

GroupRequestMoneyDialog.propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    group: PropTypes.object,
    onRequestMoney: PropTypes.func.isRequired,
};

export default GroupRequestMoneyDialog;

