import { useState } from 'react';
import PropTypes from 'prop-types';
import { Box, Container, Typography, Grid, Button, Card, CardContent, List, ListItem, ListItemText, ListItemIcon, Divider, Avatar } from '@mui/material';
import { UserPlus, DollarSign, Users, Calendar, Plus, CreditCard } from 'lucide-react';
import Sidebar from '../Dashboard/components/Sidebar';
import Header from '../Dashboard/components/Header';
import { SchedulePaymentDialog, NewPaymentDialog, MessageDialog, GroupDialog } from './components/PaymentDialogs';
import AddPayeeDialog from './components/AddPayeeDialog';
import RequestMoneyDialog from './components/RequestMoneyDialog';
import GroupRequestMoneyDialog from './components/GroupRequestMoneyDialog'; // Import the new dialog component


const PaymentOption = ({ Icon, title, description, onClick }) => (
    <Card
        sx={{
            backgroundColor: 'rgba(26, 27, 30, 0.8)',
            backdropFilter: 'blur(10px)',
            border: '1px solid rgba(255, 255, 255, 0.1)',
            borderRadius: 2,
            height: '100%',
        }}
    >
        <CardContent sx={{ height: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
            <Box>
                <Icon size={24} color="#2DD4BF" />

                <Typography variant="h6" sx={{ color: 'white', mt: 2, mb: 1 }}>
                    {title}
                </Typography>

                <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)', mb: 2 }}>
                    {description}
                </Typography>
            </Box>

            <Button
                variant="outlined"
                onClick={onClick}
                sx={{
                    color: '#2DD4BF',
                    borderColor: '#2DD4BF',
                    '&:hover': {
                        backgroundColor: 'rgba(45, 212, 191, 0.1)',
                        borderColor: '#2DD4BF',
                    },
                }}
            >
                {title}
            </Button>
        </CardContent>
    </Card>
);

PaymentOption.propTypes = {
    Icon: PropTypes.elementType.isRequired,
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
};

const ContactsList = () => {
    const [selectedContact, setSelectedContact] = useState(null);
    const [messageDialogOpen, setMessageDialogOpen] = useState(false);

    const contacts = {
        rifeFriends: [
            { id: 1, name: 'John Doe', type: 'friend' },
            { id: 2, name: 'Jane Smith', type: 'friend' },
        ],
        cards: [
            { id: 3, name: '**** 1234', type: 'card' },
            { id: 4, name: '**** 5678', type: 'card' },
        ],
        others: [
            { id: 5, name: 'Alice Johnson', type: 'other' },
            { id: 6, name: 'Bob Wilson', type: 'other' },
        ],
    };

    const handleContactClick = (contact) => {
        setSelectedContact(contact);
        setMessageDialogOpen(true);
    };

    const renderContactList = (contacts, title) => (
        <>
            <Typography variant="subtitle2" sx={{ color: 'rgba(255, 255, 255, 0.7)', px: 2, py: 1 }}>
                {title}
            </Typography>

            {contacts.map((contact) => (
                <ListItem
                    key={contact.id}
                    button
                    onClick={() => handleContactClick(contact)}
                    sx={{
                        '&:hover': {
                            backgroundColor: 'rgba(45, 212, 191, 0.1)',
                        },
                    }}
                >
                    <ListItemIcon>
                        {contact.type === 'card' ? (
                            <Box
                                sx={{
                                    width: 40,
                                    height: 40,
                                    borderRadius: '50%',
                                    border: '2px solid #2DD4BF',
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    backgroundColor: 'rgba(45, 212, 191, 0.1)',
                                }}
                            >
                                <CreditCard size={20} color="#2DD4BF" />
                            </Box>
                        ) : (
                            <Avatar sx={{ bgcolor: '#2DD4BF' }}>
                                {contact.name.charAt(0)}
                            </Avatar>
                        )}
                    </ListItemIcon>

                    <ListItemText
                        primary={contact.name}
                        primaryTypographyProps={{ color: 'white' }}
                    />
                </ListItem>
            ))}

            <Divider sx={{ backgroundColor: 'rgba(255, 255, 255, 0.1)' }} />
        </>
    );

    return (
        <>
            <List sx={{
                border: '1px solid rgba(255, 255, 255, 0.1)',
                borderRadius: 1,
                p: 1
            }}>
                {renderContactList(contacts.rifeFriends, 'Rife Friends')}

                {renderContactList(contacts.cards, 'Cards')}

                {renderContactList(contacts.others, 'Others')}
            </List>

            <MessageDialog
                open={messageDialogOpen}
                onClose={() => setMessageDialogOpen(false)}
                recipient={selectedContact}
            />
        </>
    );
};

