import { useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import {Box, Container, Typography, Grid, Card, CardContent, Button, CircularProgress, Alert} from '@mui/material';
import { BanknoteIcon as Bank, Plus } from 'lucide-react';
import Sidebar from '../Dashboard/components/Sidebar';
import Header from '../Dashboard/components/Header';
import usePlaidConnection from '../../hooks/usePlaidConnection';
import {getaccounts} from "../../api/payment_n_transaction";

const BankAccountItem = ({ account }) => (
    <Card sx={{
        backgroundColor: 'rgba(26, 27, 30, 0.8)',
        backdropFilter: 'blur(10px)',
        border: '1px solid rgba(255, 255, 255, 0.1)',
        borderRadius: 2,
        mb: 2,
    }}>
        <CardContent sx={{ p: 3 }}>
            <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 2 }}>
                <Bank size={24} style={{ color: '#2DD4BF', marginTop: 4 }} />

                <Box>
                    <Typography variant="h6" sx={{
                        color: 'white',
                        fontSize: '1.1rem',
                        fontWeight: 500,
                        mb: 0.5
                    }}>
                        {account.name} (*

                        {account.mask})
                    </Typography>

                    <Typography variant="body2" sx={{
                        color: 'rgba(255, 255, 255, 0.7)',
                        fontSize: '0.9rem'
                    }}>
                        {account.subtype.toUpperCase()} - Balance:

                        {account.balances.current}

                        {account.balances.isoCurrencyCode}
                    </Typography>
                </Box>
            </Box>
        </CardContent>
    </Card>
);

BankAccountItem.propTypes = {
    account: PropTypes.shape({
        name: PropTypes.string.isRequired,
        mask: PropTypes.string.isRequired,
        subtype: PropTypes.string.isRequired,
        balances: PropTypes.shape({
            current: PropTypes.number.isRequired,
            isoCurrencyCode: PropTypes.string.isRequired,
        }).isRequired,
    }).isRequired,
};

const BankAccountsPage = ({ mobileOpen, handleDrawerToggle }) => {
    const [bankAccounts, setBankAccounts] = useState([]);

    const handleBankConnectionSuccess = useCallback(async (data) => {
        console.log('Bank connected successfully:', data);
        const accountsData = await getaccounts(data.accessToken);
        console.log('Accounts:', accountsData);

        if (accountsData && accountsData.accounts) {
            setBankAccounts(prev => [...prev, ...accountsData.accounts]); // Changed to append new accounts
        }
    }, []);

    // Use the Plaid connection hook
    const { open, ready, status, error } = usePlaidConnection(handleBankConnectionSuccess);

    // Render initial connection UI when no accounts
    const renderInitialConnection = () => (
        <Card sx={{
            backgroundColor: 'rgba(26, 27, 30, 0.8)',
            backdropFilter: 'blur(10px)',
            border: '1px solid rgba(255, 255, 255, 0.1)',
            borderRadius: 2,
            height: '100%',
        }}>
            <CardContent sx={{ textAlign: 'center', py: 6 }}>
                <Typography variant="h4" sx={{
                    color: 'white',
                    mb: 4,
                    fontWeight: 500,
                    fontSize:'1.3rem',
                }}>
                    Connect Your Bank Account
                </Typography>

                {status === 'loading' && (
                    <Box display="flex" alignItems="center" justifyContent="center" gap={2} mb={2}>
                        <CircularProgress size={20} />

                        <Typography sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>
                            Initializing Plaid connection...
                        </Typography>
                    </Box>
                )}

                {status === 'error' && (
                    <Alert
                        severity="error"
                        sx={{ mb: 2, backgroundColor: 'rgba(211, 47, 47, 0.1)', color: '#ef5350' }}
                    >
                        {error}
                    </Alert>
                )}

                <Button
                    variant="contained"
                    fullWidth
                    onClick={() => open()}
                    disabled={!ready || status === 'loading'}
                    startIcon={<Bank size={20} />}
                    sx={{
                        backgroundColor: '#5DDCB5',
                        color: '#1A1B1E',
                        '&:hover': {
                            backgroundColor: '#4DCBA4',
                        },
                        textTransform: 'none',
                        py: 2,
                        px: 4,
                        fontSize: '1.1rem',
                        minHeight: '56px',
                        maxWidth: '400px',
                        margin: '0 auto'
                    }}
                >
                    Connect Bank Account
                </Button>

                <Typography
                    variant="body2"
                    sx={{ color: 'rgba(255, 255, 255, 0.7)', mt: 3, textAlign: 'center' }}
                >
                    Securely connect your bank account using Plaid
                </Typography>
            </CardContent>
        </Card>
    );

    return (
        <Box sx={{ display: 'flex' }}>
            <Sidebar mobileOpen={mobileOpen} handleDrawerToggle={handleDrawerToggle} />

            <Box
                component="main"
                sx={{
                    flexGrow: 1,
                    minHeight: '100vh',
                    backgroundColor: '#1A1B1E',
                    ml: { sm: '240px' },
                    width: { sm: `calc(100% - 240px)` },
                    py: 3,
                }}
            >
                <Container maxWidth="xl">
                    <Header handleDrawerToggle={handleDrawerToggle} />

                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 4 }}>
                        <Typography variant="h4" sx={{ color: 'white', flexGrow: 1 }}>
                            Bank Accounts
                        </Typography>

                        {bankAccounts.length > 0 && (
                            <Button
                                variant="contained"
                                startIcon={<Plus size={20} />}
                                onClick={() => open()}
                                disabled={!ready || status === 'loading'}
                                sx={{
                                    backgroundColor: '#2DD4BF',
                                    '&:hover': {
                                        backgroundColor: '#26b5a3',
                                    },
                                }}
                            >
                                Connect Another Account
                            </Button>
                        )}
                    </Box>

                    <Grid container spacing={3}>
                        {bankAccounts.length > 0 ? (
                            bankAccounts.map((account) => (
                                <Grid item xs={12} sm={6} md={4} key={account.account_id}>
                                    <BankAccountItem account={account} />
                                </Grid>
                            ))
                        ) : (
                            <Grid item xs={12}>
                                {renderInitialConnection()}
                            </Grid>
                        )}
                    </Grid>
                </Container>
            </Box>
        </Box>
    );
};

BankAccountsPage.propTypes = {
    mobileOpen: PropTypes.bool.isRequired,
    handleDrawerToggle: PropTypes.func.isRequired,
};

export default BankAccountsPage;

